'use client';

import { Task, useTaskStore } from '@/store/useTaskStore';
import { Draggable } from '@hello-pangea/dnd';
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card';
import { Button } from '@/components/ui/button';
import { Trash2, GripVertical, Sparkles } from 'lucide-react';
import { motion, useMotionValue, useSpring, useTransform } from 'framer-motion';
import { cn } from '@/lib/utils';
import { useState } from 'react';

interface TaskCardProps {
    task: Task;
    index: number;
    onEdit: (task: Task) => void;
}

export function TaskCard({ task, index, onEdit }: TaskCardProps) {
    const deleteTask = useTaskStore((state) => state.deleteTask);

    // 3D Tilt Effect State
    const x = useMotionValue(0);
    const y = useMotionValue(0);
    const mouseX = useSpring(x, { stiffness: 500, damping: 50 });
    const mouseY = useSpring(y, { stiffness: 500, damping: 50 });

    function onMouseMove({ currentTarget, clientX, clientY }: React.MouseEvent) {
        const { left, top, width, height } = currentTarget.getBoundingClientRect();
        x.set(clientX - left - width / 2);
        y.set(clientY - top - height / 2);
    }

    function onMouseLeave() {
        x.set(0);
        y.set(0);
    }

    const rotateX = useTransform(mouseY, [-300, 300], [5, -5]); // Inverted for tilt
    const rotateY = useTransform(mouseX, [-300, 300], [-5, 5]);

    return (
        <Draggable draggableId={task.id} index={index}>
            {(provided, snapshot) => (
                <div
                    ref={provided.innerRef}
                    {...provided.draggableProps}
                    {...provided.dragHandleProps}
                    style={{ ...provided.draggableProps.style }}
                    className={cn('mb-4 outline-none perspective-1000', snapshot.isDragging && 'z-50')}
                >
                    <motion.div
                        layout
                        initial={{ opacity: 0, scale: 0.9, y: 20 }}
                        animate={{ opacity: 1, scale: 1, y: 0 }}
                        exit={{ opacity: 0, scale: 0.9, transition: { duration: 0.2 } }}
                        style={{
                            rotateX: snapshot.isDragging ? 0 : rotateX,
                            rotateY: snapshot.isDragging ? 0 : rotateY,
                            transformStyle: "preserve-3d"
                        }}
                        onMouseMove={onMouseMove}
                        onMouseLeave={onMouseLeave}
                        className="relative"
                    >
                        {/* Glow backing */}
                        <div className="absolute inset-0 bg-gradient-to-r from-primary/20 via-accent/20 to-primary/20 rounded-xl blur-xl opacity-0 group-hover:opacity-100 transition-opacity duration-500 -z-10" />

                        <Card
                            className={cn(
                                "group relative border-0 shadow-lg cursor-default backdrop-blur-xl overflow-hidden transition-all duration-300",
                                "bg-white/10 dark:bg-black/20", // Ultra glass
                                "hover:shadow-[0_0_20px_rgba(139,92,246,0.15)]",
                                snapshot.isDragging ? "shadow-[0_0_30px_rgba(139,92,246,0.3)] ring-2 ring-primary/50 rotate-2 scale-105" : "hover:-translate-y-1"
                            )}
                            onClick={() => onEdit(task)}
                        >
                            {/* Iridescent Border Gradient */}
                            <div className="absolute inset-0 p-[1px] rounded-xl bg-gradient-to-br from-white/20 via-white/5 to-white/20 mask-border -z-0 pointer-events-none" />

                            <CardHeader className="p-4 pb-2 flex flex-row items-center justify-between space-y-0 relative z-10">
                                <div className="flex items-center gap-3">
                                    <div className="p-1.5 rounded-md bg-white/5 group-hover:bg-primary/20 transition-colors">
                                        <GripVertical className="w-3.5 h-3.5 text-muted-foreground/50 group-hover:text-primary transition-colors cursor-grab active:cursor-grabbing" />
                                    </div>
                                    <CardTitle className="text-sm font-semibold leading-tight text-foreground/90 group-hover:text-primary transition-colors tracking-tight">
                                        {task.title}
                                    </CardTitle>
                                </div>
                                <Button
                                    variant="ghost"
                                    size="icon"
                                    className="h-7 w-7 text-muted-foreground/50 opacity-0 group-hover:opacity-100 transition-all hover:text-white hover:bg-destructive shadow-sm"
                                    onClick={(e) => {
                                        e.stopPropagation();
                                        deleteTask(task.id);
                                    }}
                                >
                                    <Trash2 className="h-3.5 w-3.5" />
                                </Button>
                            </CardHeader>
                            <CardContent className="p-4 pt-1 pl-12 relative z-10">
                                {task.description && (
                                    <p className="text-xs text-muted-foreground/80 line-clamp-2 mb-3 font-medium leading-relaxed">
                                        {task.description}
                                    </p>
                                )}
                                <div className="flex items-center justify-between pt-2 border-t border-white/5">
                                    <div className="flex items-center gap-1.5">
                                        <div className={cn("w-1.5 h-1.5 rounded-full",
                                            task.status === 'todo' ? "bg-slate-500" :
                                                task.status === 'in-progress' ? "bg-primary animate-pulse shadow-[0_0_8px_rgba(139,92,246,0.8)]" :
                                                    "bg-emerald-400 shadow-[0_0_8px_rgba(52,211,153,0.8)]"
                                        )} />
                                        <span className="text-[10px] uppercase font-bold text-muted-foreground/60 tracking-wider">
                                            {task.status.replace('-', ' ')}
                                        </span>
                                    </div>
                                    <div className="text-[10px] font-mono text-muted-foreground/40 group-hover:text-primary/60 transition-colors">
                                        {new Date(task.createdAt).toLocaleDateString(undefined, { month: 'short', day: 'numeric' })}
                                    </div>
                                </div>
                            </CardContent>
                        </Card>
                    </motion.div>
                </div>
            )}
        </Draggable>
    );
}
