'use client';

import { Task, TaskStatus } from '@/store/useTaskStore';
import { Droppable } from '@hello-pangea/dnd';
import { TaskCard } from './TaskCard';
import { cn } from '@/lib/utils';

interface ColumnProps {
    id: TaskStatus;
    title: string;
    tasks: Task[];
    onEdit: (task: Task) => void;
}

export function Column({ id, title, tasks, onEdit }: ColumnProps) {
    return (
        <div className="h-full flex flex-col bg-white/5 backdrop-blur-sm rounded-2xl border border-white/10 shadow-[0_8px_32px_rgba(0,0,0,0.1)] transition-all hover:bg-white/10 group">
            {/* Header */}
            <div className="p-5 pb-3 flex justify-between items-center border-b border-white/5">
                <h3 className="text-sm font-bold tracking-widest text-foreground/90 uppercase flex items-center gap-3">
                    <div className={cn("w-2 h-2 rounded-full shadow-[0_0_10px]",
                        id === 'todo' ? "bg-slate-400 shadow-slate-400/50" :
                            id === 'in-progress' ? "bg-primary shadow-primary/80 animate-pulse" :
                                "bg-emerald-400 shadow-emerald-400/80"
                    )} />
                    {title}
                </h3>
                <span className="bg-white/10 text-xs font-mono font-medium px-2.5 py-1 rounded-md border border-white/5 backdrop-blur-md shadow-inner text-muted-foreground group-hover:text-foreground transition-colors">
                    {tasks.length}
                </span>
            </div>

            {/* Droppable Area */}
            <div className="p-3 flex-1 overflow-y-auto min-h-[500px] scrollbar-hide">
                <Droppable droppableId={id}>
                    {(provided, snapshot) => (
                        <div
                            {...provided.droppableProps}
                            ref={provided.innerRef}
                            className={cn(
                                'h-full rounded-xl transition-all duration-300 p-1',
                                snapshot.isDraggingOver ? 'bg-primary/10 ring-1 ring-primary/30 shadow-[inset_0_0_20px_rgba(139,92,246,0.1)]' : 'bg-transparent'
                            )}
                        >
                            {tasks.map((task, index) => (
                                <TaskCard
                                    key={task.id}
                                    task={task}
                                    index={index}
                                    onEdit={onEdit}
                                />
                            ))}
                            {provided.placeholder}
                        </div>
                    )}
                </Droppable>
            </div>
        </div>
    );
}
