'use client';

import { useEffect, useState } from 'react';
import { DragDropContext, DropResult } from '@hello-pangea/dnd';
import { useTaskStore, TaskStatus, Task } from '@/store/useTaskStore';
import { Column } from './Column';

interface BoardProps {
    onEditTask: (task: Task) => void;
}

export function Board({ onEditTask }: BoardProps) {
    const [isMounted, setIsMounted] = useState(false);
    const tasks = useTaskStore((state) => state.tasks);
    const searchQuery = useTaskStore((state) => state.searchQuery);
    const moveTask = useTaskStore((state) => state.moveTask);

    useEffect(() => {
        setIsMounted(true);
    }, []);

    if (!isMounted) {
        return <div className="grid grid-cols-1 md:grid-cols-3 gap-6 h-full p-4">
            <div className="h-full bg-muted/50 rounded-lg animate-pulse" />
            <div className="h-full bg-muted/50 rounded-lg animate-pulse" />
            <div className="h-full bg-muted/50 rounded-lg animate-pulse" />
        </div>;
    }

    const filteredTasks = tasks.filter((t) =>
        t.title.toLowerCase().includes(searchQuery.toLowerCase())
    );

    const columns: { id: TaskStatus; title: string }[] = [
        { id: 'todo', title: 'To Do' },
        { id: 'in-progress', title: 'In Progress' },
        { id: 'finished', title: 'Finished' },
    ];

    const onDragEnd = (result: DropResult) => {
        const { destination, source, draggableId } = result;

        if (!destination) return;

        if (
            destination.droppableId === source.droppableId &&
            destination.index === source.index
        ) {
            return;
        }

        if (destination.droppableId !== source.droppableId) {
            moveTask(draggableId, destination.droppableId as TaskStatus);
        }
    };

    return (
        <DragDropContext onDragEnd={onDragEnd}>
            <div className="grid grid-cols-1 md:grid-cols-3 gap-6 h-full overflow-hidden">
                {columns.map((col) => (
                    <Column
                        key={col.id}
                        id={col.id}
                        title={col.title}
                        tasks={filteredTasks.filter((t) => t.status === col.id)}
                        onEdit={onEditTask}
                    />
                ))}
            </div>
        </DragDropContext>
    );
}
