'use client';

import { useState } from 'react';
import { Board } from '@/components/board/Board';
import { TaskForm } from '@/components/board/TaskForm';
import { useTaskStore, Task } from '@/store/useTaskStore';
import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import { Plus, Search, LayoutTemplate } from 'lucide-react';

export default function Home() {
  const [isDialogOpen, setIsDialogOpen] = useState(false);
  const [taskToEdit, setTaskToEdit] = useState<Task | null>(null);

  const searchQuery = useTaskStore((state) => state.searchQuery);
  const setSearchQuery = useTaskStore((state) => state.setSearchQuery);

  const handleAddTask = () => {
    setTaskToEdit(null);
    setIsDialogOpen(true);
  };

  const handleEditTask = (task: Task) => {
    setTaskToEdit(task);
    setIsDialogOpen(true);
  };

  return (
    <main className="min-h-screen bg-background flex flex-col font-sans text-foreground relative overflow-hidden selection:bg-primary/30 selection:text-primary-foreground">
      {/* Animated Aurora Background */}
      <div className="fixed inset-0 -z-10 bg-[radial-gradient(ellipse_at_top,_var(--tw-gradient-stops))] from-indigo-900/20 via-background to-background pointer-events-none" />
      <div className="fixed top-0 left-0 w-full h-[500px] opacity-30 pointer-events-none mix-blend-screen overflow-hidden">
        <div className="absolute top-[-50%] left-[-20%] w-[70%] h-[200%] bg-primary/30 rounded-[100%] blur-[120px] animate-[aurora_20s_infinite_linear]" />
        <div className="absolute top-[-30%] right-[-20%] w-[60%] h-[180%] bg-accent/20 rounded-[100%] blur-[100px] animate-[aurora_25s_infinite_reverse_linear]" />
      </div>

      {/* Header */}
      <header className="px-6 py-5 flex flex-col md:flex-row items-center justify-between gap-6 sticky top-0 z-50 
        bg-background/40 backdrop-blur-2xl border-b border-white/10 shadow-[0_4px_30px_rgba(0,0,0,0.1)]">
        <div className="flex items-center gap-3 group cursor-pointer">
          <div className="relative">
            <div className="absolute inset-0 bg-primary blur-lg opacity-50 group-hover:opacity-100 transition-opacity duration-500" />
            <div className="relative bg-black/20 p-2.5 rounded-xl border border-white/10 backdrop-blur-md">
              <LayoutTemplate className="w-6 h-6 text-primary group-hover:text-white transition-colors duration-300" />
            </div>
          </div>
          <div>
            <h1 className="text-2xl font-black tracking-tighter bg-clip-text text-transparent bg-gradient-to-r from-foreground via-primary to-accent animate-gradient-x">
              TASKER
            </h1>
          </div>
        </div>

        <div className="flex items-center gap-5 w-full md:w-auto">
          <div className="relative w-full md:w-96 group">
            <div className="absolute inset-0 bg-gradient-to-r from-primary/20 to-accent/20 rounded-lg blur opacity-0 group-focus-within:opacity-100 transition-opacity duration-500" />
            <Search className="absolute left-3 top-1/2 -translate-y-1/2 h-4 w-4 text-muted-foreground group-focus-within:text-primary z-10 transition-colors" />
            <Input
              placeholder="System Search..."
              className="pl-10 h-10 bg-white/5 border-white/10 focus-visible:ring-0 focus-visible:border-primary/50 transition-all font-mono text-sm shadow-inner relative z-0 placeholder:text-muted-foreground/50"
              value={searchQuery}
              onChange={(e) => setSearchQuery(e.target.value)}
            />
          </div>
          <Button
            onClick={handleAddTask}
            className="group relative overflow-hidden bg-primary/90 hover:bg-primary border border-white/10 shadow-[0_0_20px_rgba(124,58,237,0.3)] hover:shadow-[0_0_30px_rgba(124,58,237,0.6)] transition-all duration-300"
          >
            <div className="absolute inset-0 bg-gradient-to-r from-transparent via-white/20 to-transparent translate-x-[-100%] group-hover:translate-x-[100%] transition-transform duration-700 ease-in-out" />
            <Plus className="w-4 h-4 mr-2" />
            <span className="font-semibold tracking-wide">NEW ENTRY</span>
          </Button>
        </div>
      </header>

      {/* Board Content */}
      <div className="flex-1 overflow-hidden p-6">
        <Board onEditTask={handleEditTask} />
      </div>

      {/* Task Form Dialog */}
      <TaskForm
        open={isDialogOpen}
        onOpenChange={setIsDialogOpen}
        taskToEdit={taskToEdit}
      />
    </main>
  );
}
